.contentLeft_home {
    width: 9rem;
}

.devHome_nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 93vw;
}

.devHome_icon {
    margin: 0 5px;
}

.devHome_body {
    padding: 1.4rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: radial-gradient(circle at 10% 20%, rgb(64, 84, 178) 0%, rgb(219, 2, 234) 90%);
}

.codeBlogs_  {
    font-size: 2.5rem;
    font-weight: 600;
    color: white;
}

.rohan_ {
    display: block;
    color: white;
    text-align: right;
    font-size: 13px;
}

.devHome_search {
    background-color: transparent;
    outline: none;
    font-size: 1.2rem;
    margin: 5px;
    background-color: transparent;
    padding: 8px;
    border: none;
    color: white;
    font-weight: bold;
    border-bottom: 2px solid white;
    width: 35rem;
}

.fauser {
    color: white;
    font-size: 2.3rem;
    cursor: pointer;
}

.profile_image {
    border-radius: 50%;
    border: 1px solid black;
    height: 4rem;
    width: 4rem;
}

.profile_name {
    color: white;
    font-size: 1.5rem;
    font-weight: bolder;
}

.profile_email {
    color: white;
    font-size: 12px;
    letter-spacing: 1px;
}

.devHome_profile {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    min-width: 13rem;
}

.devHome_content {
    width: 80vw;
    min-height: 110vh;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: 4rem 4rem;
    padding: 1rem;
    background-color: rgb(34, 34, 34);
    border-radius: 2rem;
    display: flex;
}

.noContent {
    color: white;
    z-index: 99999;
    padding-top: 8rem;
    width: 60vw;
    text-align: center;
    font-size: 4.5rem;
}

.left_btn {
    width: 7rem;
    height: 3rem;
    color: white;
    display: flex;
    align-items: center;
    padding: 0 1rem;
}

.contentBlogs_flexBody {
    display: flex;
    flex-wrap: wrap;
    margin-left: 2rem;
}

.sad {
    font-size: 3.2rem;
} 

